.footer{
  padding: 45px 0 65px;
  .container{
    display: flex;
    align-items: center;
    justify-content: space-between;
    @media all and (min-width: 1200px) {
      max-width: 1202px;
      position: relative;
      left: -16px;
    }
  }

  &__logo{
    width: 156px;
    img{
      display: block;
    }
  }

  &__link{
    display: inline-block;
    color: #000000;
    font-family: $font4;
    font-size: 14px;
    font-weight: 400;
    border-bottom: 1px dashed #000000;
    cursor: pointer;
    &:hover{
      border-color: transparent;
    }
  }

  &__developer{
    display: flex;
    align-items: flex-end;
    margin-bottom: 13px;
    &__label{
      color: #000000;
      font-family: $font4;
      font-size: 11px;
      font-weight: 400;
    }
    &__img{
      display: block;
      width: 73px;
      margin-left: 14px;
    }
  }

  &__social-block{
    display: flex;
    align-items: center;
    &__link{
      display: block;
      height: 24px;
      svg{
        display: block;
        width: 100%;
        height: 100%;
        object-fit: contain;
        path{
          fill: #c2c2c2;
        }
      }
      &_type_vk{
        width: 25px;
      }
      &_type_insta{
        width: 18px;
        margin-left: 10px;
      }
      &:hover{
        svg{
          path{
            fill: #00aeff;
          }
        }
      }
    }
  }

  &__contacts{
    display: flex;
    align-items: center;
    &__right{
      margin-left: 10px;
    }
  }
}
