@import url(partials/reset.css);

@import url(partials/fonts.css);

/* my style */

a {
  text-decoration: none;
}

html {
  font-family: sans-serif;
  line-height: 1.2;
}

body {
  margin: 0;
  font-size: 1rem;
  color: #000000;
  font-weight: 400;
  font-family: "PT Serif Pro";
  text-align: left;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.container {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
  max-width: 1170px;
}

img {
  max-width: 100%;
  height: auto;
}

.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 179px;
  height: 51px;
  border-radius: 63% 8% 44% 10%/86% 23% 88% 24%;
  background: #da1f8d;
  box-shadow: 0 26px 46px rgba(0, 0, 0, 0.24), 0 3px 0 #952165;
  font-family: Roboto;
  color: #ffffff;
  font-size: 12px;
  font-weight: 700;
}

.site-header {
  padding-top: 25px;
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  max-width: 1156px;
  width: calc(100% - 30px);
  z-index: 10;
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}

.site-header--scrolled {
  height: 71px;
  box-shadow: 0 18px 46px rgba(0, 0, 0, 0.2), 0 3px 0 rgba(66, 106, 173, 0.48);
  border-radius: 10px;
  background-color: #ffffff;
  padding: 0 27px;
}

.site-header--scrolled .sh-logo {
  width: 153px;
}

.site-header--scrolled .shc-work-time {
  display: none;
}

.site-header--scrolled .shc-work-phone {
  line-height: 131%;
}

.site-header--scrolled .sh-contacts__right .btn {
  width: 146px;
  height: 42px;
}

.sh-burger {
  display: none;
}

.sh-logo {
  display: block;
  width: 191px;
}

.sh-menu {
  display: flex;
  align-items: center;
}

.sh-menu__link {
  margin: 0 7.5px;
  color: #000000;
  font-family: Montserrat;
  font-weight: 400;
  padding-bottom: 8px;
  font-size: 14px;
}

.sh-menu__link--active {
  border-bottom: 1px solid #000;
}

.sh-menu__link:hover {
  color: #da1f8d;
}

.sh-contacts {
  display: flex;
}

.sh-contacts__left {
  margin: 0 21px;
}

.shc-work-time {
  font-family: Montserrat;
  color: #da1f8d;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  margin-right: 11px;
  margin-bottom: 4px;
  margin-top: 6px;
}

.shc-work-time svg {
  width: 11px;
  margin-right: 9px;
  height: 11px;
  fill: #da1f8d;
}

.shc-work-phone {
  display: flex;
  align-items: center;
  font-family: Montserrat;
  color: #000000;
  font-size: 26px;
  font-weight: 600;
}

.shc-work-phone img {
  width: 11px;
  height: 11px;
  margin-right: 9px;
}

.section-1 {
  /* background: url('../images/bg-section-1.jpg') no-repeat top left;
    background-size: 100% auto;*/
  max-width: 1920px;
  margin: 0 auto;
  position: relative;
  overflow: hidden;
}

.section-1__content {
  z-index: 3;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  padding-top: 162px;
}

.s1-text-1 {
  text-shadow: -1px 3px 0 rgba(0, 0, 0, 0.15);
  font-family: "Deja Vu Serif";
  color: #ff781e;
  font-size: 44px;
  font-style: italic;
  font-weight: 700;
}

.s1-text-2 {
  font-family: "PT Serif Pro";
  color: #000000;
  font-size: 24px;
  font-style: italic;
  font-weight: 700;
  margin-top: 20px;
}

.s1-decoration-1 {
  z-index: 2;
  position: absolute;
  right: 0;
  bottom: 0;
}

.s1-card {
  position: absolute;
  box-shadow: 0 4px 8px 1px rgba(0, 103, 169, 0.18);
  border-radius: 4px;
  background-color: #ffffff;
  padding: 62px 34px 47px;
  width: 408px;
  max-width: 100%;
  position: relative;
  margin-top: 126px;
}

.s1-card__decoration-1 {
  position: absolute;
  bottom: -100px;
  left: calc(100% - 67px);
  max-width: 850px;
}

.s1-card__decoration-2 {
  position: absolute;
  bottom: -57px;
  left: -120px;
}

.s1-card__decoration-3 {
  position: absolute;
  bottom: calc(100% - 10px);
  left: 50%;
  transform: translateX(-50%);
}

.s1-card__decoration-4 {
  position: absolute;
  bottom: -225px;
  left: 50%;
  transform: translateX(-50%);
}

.s1-card__text-1 {
  font-family: "PT Serif Pro";
  color: #f86660;
  font-size: 16px;
  font-style: italic;
  font-weight: 700;
  text-align: center;
}

.s1-card__text-2 {
  font-family: "PT Serif Pro";
  color: #000000;
  font-size: 17px;
  font-weight: 800;
  text-align: center;
  margin-top: 46px;
  margin-bottom: 59px;
}

.s1-card .btn {
  margin: 0 auto;
}

.section-2 {
  text-align: center;
}

.section-2__text-1 {
  font-family: "Deja Vu Serif";
  color: #2f313f;
  font-size: 44px;
  font-style: italic;
  font-weight: 700;
  margin-top: 15px;
}

.section-2__text-2 {
  font-family: "Deja Vu Serif";
  color: #000000;
  font-size: 22px;
  font-style: italic;
}

.s2-fb {
  margin-top: 100px;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.s2-fb--tc-gren {
  color: #00a413;
}

.s2-fb--tc-blue {
  color: #00a7ff;
}

.s2-fb__title {
  font-family: "Deja Vu Serif";
  color: #2f313f;
  font-size: 33px;
  font-weight: 700;
}

.s2-fb__old-price {
  font-family: "Deja Vu Serif";
  font-size: 24px;
  font-weight: 400;
  text-decoration: line-through;
  margin-top: 43px;
}

.s2-fb__new-pice {
  font-family: "Deja Vu Serif";
  font-size: 46px;
  font-weight: 700;
}

.s2-fb__left,
.s2-fb__right {
  padding-top: 50px;
}

.footer {
  padding: 45px 0 65px;
}

.footer .container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.footer__logo {
  width: 156px;
}

.footer__logo img {
  display: block;
}

.footer__link {
  display: inline-block;
  color: #000000;
  font-family: "Roboto";
  font-size: 14px;
  font-weight: 400;
  border-bottom: 1px dashed #000000;
  cursor: pointer;
}

.footer__link:hover {
  border-color: transparent;
}

.footer__developer {
  display: flex;
  align-items: flex-end;
  margin-bottom: 13px;
}

.footer__developer__label {
  color: #000000;
  font-family: "Roboto";
  font-size: 11px;
  font-weight: 400;
}

.footer__developer__img {
  display: block;
  width: 73px;
  margin-left: 14px;
}

.footer__social-block {
  display: flex;
  align-items: center;
}

.footer__social-block__link {
  display: block;
  height: 24px;
}

.footer__social-block__link svg {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.footer__social-block__link svg path {
  fill: #c2c2c2;
}

.footer__social-block__link_type_vk {
  width: 25px;
}

.footer__social-block__link_type_insta {
  width: 18px;
  margin-left: 10px;
}

.footer__social-block__link:hover svg path {
  fill: #00aeff;
}

.footer__contacts {
  display: flex;
  align-items: center;
}

.footer__contacts__right {
  margin-left: 10px;
}

@media all and (min-width: 1200px) {
  .footer .container {
    max-width: 1202px;
    position: relative;
    left: -16px;
  }
}

@media screen and (max-width: 1850px) {
  .s1-card__decoration-4 {
    display: none;
  }
}

@media screen and (max-width: 1487px) {
  .section-1-bg {
    max-width: 100%;
    height: 100%;
    width: auto;
    object-fit: cover;
    object-position: top left;
  }

  .s1-decoration-1 {
    display: none;
  }

  .s1-card__decoration-2 {
    bottom: -32px;
    left: -54px;
    width: 112px;
  }

  .s1-card__decoration-1 {
    position: absolute;
    bottom: -103px;
    left: calc(100% - 67px);
    max-width: 57vw;
  }
}

@media screen and (max-width: 1200px) {
  .sh-burger {
    display: block;
    width: 30px;
    height: 18px;
    cursor: pointer;
    position: relative;
  }

  .sh-burger span,
  .sh-burger span::before,
  .sh-burger span::after {
    position: absolute;
    top: 50%;
    margin-top: -1px;
    left: 50%;
    margin-left: -15px;
    width: 30px;
    height: 2px;
    background-color: #000;
  }

  .sh-burger span::before,
  .sh-burger span::after {
    content: '';
    transition: 0.2s;
  }

  .sh-burger span::before {
    transform: translateY(-8px);
  }

  .sh-burger span::after {
    transform: translateY(8px);
  }

  .sh-burger--active span {
    height: 0;
  }

  .sh-burger--active span::before {
    transform: rotate(45deg);
  }

  .sh-burger--active span::after {
    transform: rotate(-45deg);
  }

  .sh-logo,
  .site-header--scrolled .sh-logo {
    width: 250px;
  }

  .site-header {
    height: 71px;
    box-shadow: 0 18px 46px rgba(0, 0, 0, 0.2), 0 3px 0 rgba(66, 106, 173, 0.48);
    border-radius: 10px;
    background-color: #ffffff;
    padding: 0 27px;
  }

  .site-header .sh-logo {
    width: 153px;
  }

  .site-header .shc-work-time {
    display: none;
  }

  .site-header .shc-work-phone {
    line-height: 131%;
  }

  .site-header .sh-contacts__right .btn {
    width: 146px;
    height: 42px;
  }

  .sh-menu {
    display: none;
    position: absolute;
    box-shadow: 0 18px 46px rgba(0, 0, 0, 0.2), 0 3px 0 rgba(66, 106, 173, 0.48);
    border-radius: 10px;
    flex-direction: column;
    align-items: center;
    top: 74px;
    background: #fff;
    width: 100%;
    left: 0;
  }

  .sh-menu__link {
    display: block;
    text-align: center;
    margin: 10px auto;
    color: #000000;
    font-family: Montserrat;
    font-weight: 400;
    padding-bottom: 8px;
    font-size: 14px;
  }

  .sh-menu__link--active {
    border-bottom: 1px solid #000;
  }

  .sh-menu__link:hover {
    color: #da1f8d;
  }

  .sh-contacts {
    display: none;
  }
}

@media only screen and (max-width: 1199px) {
  .container {
    max-width: 960px;
  }
}

@media only screen and (max-width: 991px) {
  .container {
    max-width: 720px;
  }
}

@media (max-width: 991px) {
  .s1-card__decoration-1 {
    bottom: -45px;
    max-width: 47vw;
  }
}

@media only screen and (max-width: 767px) {
  .container {
    max-width: 320px;
  }

  .container.section-1__content {
    max-width: 100%;
  }

  .s1-card__decoration-1 {
    bottom: -45px;
    left: calc(100% - 92px);
    max-width: 524px;
  }

  .section-1__content {
    padding-top: 100px;
  }

  .s1-text-1 {
    font-size: 27px;
    text-shadow: -1px 1px 0 rgba(0, 0, 0, 0.15);
  }

  .s1-text-2 {
    font-size: 18px;
    margin-top: 15px;
  }

  .s1-card__text-2 {
    font-family: "PT Serif Pro";
    color: #000;
    font-size: 16px;
    font-weight: 800;
    text-align: center;
    margin-top: 30px;
    margin-bottom: 40px;
  }

  .s1-card {
    padding: 35px 20px 47px;
    left: 50%;
    transform: translateX(-50%);
  }

  .s1-card__decoration-1 {
    top: calc(100% - -17px);
    bottom: auto;
    left: 50%;
    transform: translateX(-50%);
    width: 524px;
    max-width: 100%;
  }

  .s1-text-1,
  .s1-text-2 {
    text-align: center;
  }
}

@media (max-width: 767px) {
  .section-2__text-1 {
    font-size: 27px;
  }

  .section-2__text-2 {
    font-size: 18px;
    margin-top: 5px;
  }

  .s2-fb {
    flex-direction: column;
    align-items: center;
    margin-top: 0;
  }

  .s2-fb__right {
    order: -1;
    padding-top: 20px;
  }

  .s2-fb__left {
    order: -2;
  }

  .s2-fb__title {
    font-size: 24px;
  }

  .s2-fb__old-price {
    font-size: 23px;
    margin-top: 20px;
  }

  .s2-fb__new-pice {
    font-size: 36px;
  }

  .s2-fb__center {
    margin-top: 10px;
  }

  .s2-fb__center img {
    width: 200px;
  }
}

@media screen and (max-width: 500px) {
  .sh-logo,
  .site-header--scrolled .sh-logo {
    width: 173px;
  }

  .site-header,
  .site-header--scrolled {
    width: 100%;
  }
}

@media only screen and (max-width: 450px) {
  .s1-card {
    margin-top: 83px;
    padding-bottom: 24px;
  }

  .s1-card__text-1 {
    font-size: 14px;
  }

  .s1-card__text-2 {
    font-size: 14px;
    margin-top: 12px;
    margin-bottom: 22px;
  }

  .s1-card__decoration-3 {
    width: 160px;
  }

  .s1-card__decoration-2 {
    display: none;
  }

  .s1-text-1 {
    font-size: 22px;
  }

  .s1-text-2 {
    font-size: 16px;
  }
}